<template>
  <v-app>
    <v-select
      v-model="selectedSet"
      :items="trainingSets"
    ></v-select>
    <v-container v-if="isDataLoaded">
      <v-row>
        <v-col cols="6">
          <linePlot 
            :key="filteredData[0].timestamp.getTime()"
            :data="filteredData"
            :xName="xName"
            yName="temperature"
            :originalData="data"
            :width="600"
            :height="200"
            ></linePlot>

        </v-col>
        <v-col cols="6">
          <linePlot 
            :key="filteredData[0].timestamp.getTime()"
            :data="filteredData"
            :xName="xName"
            yName="magnetism"
            :originalData="data"
            :width="600"
            :height="200"
            ></linePlot>

        </v-col>
      </v-row>
      <v-row>
        <v-col cols="6">
          <linePlot 
            :key="filteredData[0].timestamp.getTime()"
            :data="filteredData"
            :xName="xName"
            yName="sun spots"
            :originalData="data"
            :width="600"
            :height="200"
            ></linePlot>

        </v-col>
        <v-col cols="6">
          <linePlot 
            :key="filteredData[0].timestamp.getTime()"
            :data="filteredData"
            :xName="xName"
            yName="speed"
            :originalData="data"
            :width="600"
            :height="200"
            ></linePlot>

        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <histogram 
            :data="data"
            :key="selectedSet"
            :xName="xName"
            yName="dst"
            :width="1200"
            :height="150"
            @extent="filterData"
          >
          </histogram>
        </v-col>
      </v-row>
    </v-container>
    <div id="loading" v-else>Loading...</div>
  </v-app>
</template>

<script>
import linePlot from './components/linePlot'
import histogram from './components/histogram'
import {csvParse} from 'd3';

const solarWindUrl = 'https://gist.githubusercontent.com/ahmed-com/057c619e68a176b1513c96770e3b8889/raw/92ac0c7088fd06fb7b74aabf24032f02de7eb5df/data.csv'

export default {
  name: 'App',

  components:{
    linePlot,
    histogram
  },

  data(){
    return {
      xName: 'timestamp',
      isDataLoaded: false,
      allDataSets: null,
      filteredData: null,
      selectedSet: 'train_a',
      trainingSets: [
        'train_a',
        'train_b',
        'train_c'
      ]
    }
  },

  computed: {
    data(){
      if(!this.isDataLoaded) return null;

      return this.allDataSets[this.selectedSet];
    }
  },

  methods: {
    filterData([min,max]){
      // console.log(min , max)
      this.filteredData =  this.data.filter(d => {
        const x = d[this.xName];
        return x > min && x < max;
      });
      // console.log(this.filteredData);
    },

    timedeltaToDate(timedelta){
      const dateParts = timedelta.split(' ');
      const days = dateParts[0];
      const timeParts = dateParts[2].split(':');
      const hours = timeParts[0];
      const minutes = timeParts[1];
      const seconds = timeParts[2];
      const date = (seconds * 1000) + (minutes * 1000 * 60) + (hours * 1000 * 60 * 60) + (days * 1000 * 60 * 60 * 24);
      return new Date(date);
    },

    sanitizeRow(row){
      row.timestamp = this.timedeltaToDate(row.timedelta);
      row.dst = +row['t0'];
      row.temperature = +row['temperature_mean'];
      row.magentism = +row['bt_mean'];
      row['sun spots'] = +row['smoothed_ssn'];
      row.speed = +row['speed_mean'];

      return row;
    },

    splitIntoDataSets(fullData){
      this.allDataSets = {
        'train_a': [],
        'train_b': [],
        'train_c': []
      };

      fullData.forEach(row => {
        if(this.allDataSets[row.period].length > 1000) return;
        this.allDataSets[row.period].push(this.sanitizeRow(row));
      });

      this.isDataLoaded = true;
      this.filteredData = this.data;
      console.log("the first row:", this.allDataSets['train_a'][0]);
      console.log("a data row:",this.data[0])
    },
  },

  async mounted(){
    const dataCache = await caches.open('data-cache');
    let response = await dataCache.match(solarWindUrl);

    if(!response){
      response = await fetch(solarWindUrl);
      dataCache.add(solarWindUrl);
    }

    const csvData = await response.text();
    const fullData = csvParse(csvData);

    console.log("before splitting:", fullData[0]);
    this.splitIntoDataSets(fullData);
  }
};
</script>
<style scoped>
#loading{
  font-size: 10em;
}
</style>