<template>
    <svg id="histogram" :width="width" :height="height">
        <g :transform="histogramPosition">
            <text
                class="axis-label"
                textAnchor="middle"
                :transform="bottomTextPosition"
            >
                {{yName}}
            </text>
            <g 
                v-for="tick in yTicks"
                :key="+tick"
                class="tick"
                :transform="getLeftTickPosition(tick)"
            >
                <line :x2="innerWidth" />
                <text
                    textAnchor="end"
                    dy=".32em"
                    :x="leftLabelPosition"
                >
                    {{tick}}
                </text>
            </g>
            <g class="histogram">
                <rect
                    v-for="d in dataSet"
                    :key="extractXValue(d).getTime()"
                    :y="innerHeight - scaleY(extractYValue(d))"
                    :x="scaleX(extractXValue(d))"
                    :width="innerWidth / data.length"
                    :height="scaleY(extractYValue(d))"
                >

                </rect>
            </g>
            <g ref="brush"></g>
        </g>
    </svg>
</template>

<script>
import { scaleLinear, scaleTime, timeFormat, extent, brushX, select, event} from 'd3'

export default {
    props: [
        "xName",
        "yName",
        "width",
        "height",
        "data"
    ],

    data(){
        return {
            xAxisLabelOffset: 50,
            yAxisLabelOffset: 45,
            tickOffset: 7,
            margin: {
                top: 20,
                right: 0,
                bottom: 0,
                left: 90
            },
            dataSet: null
        }
    },

    computed:{
        brush(){
            return brushX().extent([[0,0], [this.innerWidth, this.innerHeight]]);
        },

        histogramPosition(){
            return `translate(${this.margin.left},${this.margin.top})`
        },

        innerWidth(){
            const result = this.width - this.margin.left - this.margin.right;
            return result;
        },

        innerHeight(){
            const result = this.height - this.margin.top - this.margin.bottom;
            return result
        },

        yTicks(){
            return this.scaleY.ticks();
        },

        bottomLabelPosition(){
            return this.innerHeight + this.tickOffset;
        },

        bottomTextPosition(){
            return `translate(${-this.yAxisLabelOffset},${this.innerHeight }) rotate(-90)`
        },

        leftLabelPosition(){
            return -this.tickOffset;
        },

        leftTextX(){
            return this.innerWidth / 2;
        },

        leftTextY(){
            return this.innerHeight + this.xAxisLabelOffset;
        },
        
        scaleX(){
            return scaleTime()
                .domain(extent(this.dataSet,this.extractXValue))
                .range([0, this.innerWidth])
        },
        
        scaleY(){
            return scaleLinear()
                .domain(extent(this.dataSet, this.extractYValue))
                .range([0,this.innerHeight])
                .nice();
        }
    },

    methods:{
        bottomTickFormat: timeFormat('%a'),

        extractXValue(d){
            return d[this.xName];
        },

        extractYValue(d){
            return d[this.yName];
        },

        getBottomTickPosition(tick){
            return `translate(${this.scaleX(tick)},0)`;
        },

        getLeftTickPosition(tick){
            return `translate(0,${this.innerHeight - this.scaleY(tick)})`;
        }
    },

    beforeMount(){
        this.dataSet = this.data;
        console.log('histogram b4 mounted')
    },

    mounted(){
        console.log('histogram mounted')
        const vm = this;
        this.brush(select(this.$refs.brush));
        this.brush.on('brush end',()=>{
            vm.$emit('extent', event.selection && event.selection.map(vm.scaleX.invert))
        })
    }
}
</script>

<style scoped>
body {
  margin: 0;
  overflow: hidden;
  font-family: 'Poppins', sans-serif;
}

.tick line {
  stroke: #C0C0BB;
}

.tick text {
  fill: #635F5D;
  font-size: 50%;
}

.histogram rect {
  stroke: steelblue;
}

.axis-label {
  font-size: 130%;
  fill: #635F5D;
}
</style>