<template>
    <svg id="graph" ref="graph" @click="downloadGraph" :width="width" :height="height">
        <g :transform="graphPosition">
            <g
                v-for="tick in xTicks"
                class="tick"
                :key="+tick"
                :transform="getBottomTickPosition(tick)"
            >
                <line :y2="innerHeight" />
                <text
                    textAnchor="middle"
                    dy=".71em"
                    :y="bottomLabelPosition"
                >
                    {{bottomTickFormat(tick)}}
                </text>
            </g>
            <text
                class="axis-label"
                textAnchor="middle"
                :transform="bottomTextPosition"
            >
                {{yName}}
            </text>
            <g 
                v-for="tick in yTicks"
                :key="+tick"
                class="tick"
                :transform="getLeftTickPosition(tick)"
            >
                <line :x2="innerWidth" />
                <text
                    textAnchor="end"
                    dy=".32em"
                    :x="leftLabelPosition"
                >
                    {{tick}}
                </text>
            </g>
            <g class="line-plot">
                <path 
                    fill="none"
                    stroke="steelblue"
                    :d="linePath"
                />
            </g>
        </g>
    </svg>
</template>

<script>
import { scaleLinear, scaleTime, timeFormat, extent, line} from 'd3'

export default {
    props: [
        "xName",
        "yName",
        "originalData",
        "width",
        "height",
        "data"
    ],

    data(){
        return {
            xAxisLabelOffset: 50,
            yAxisLabelOffset: 45,
            tickOffset: 7,
            margin: {
                top: 0,
                right: 30,
                bottom: 65,
                left: 90
            },
            dataSet: null,
            graph: null
        }
    },

    computed:{
        graphPosition(){
            return `translate(${this.margin.left},${this.margin.top})`
        },

        innerWidth(){
            const result = this.width - this.margin.left - this.margin.right;
            return result;
        },

        innerHeight(){
            const result = this.height - this.margin.top - this.margin.bottom;
            return result
        },

        xTicks(){
            return this.scaleX.ticks();
        },

        yTicks(){
            return this.scaleY.ticks();
        },

        bottomLabelPosition(){
            return this.innerHeight + this.tickOffset;
        },

        bottomTextPosition(){
            return `translate(${-this.yAxisLabelOffset},${this.innerHeight }) rotate(-90)`
        },

        leftLabelPosition(){
            return -this.tickOffset;
        },

        leftTextX(){
            return 0;
        },

        leftTextY(){
            return this.innerHeight + this.xAxisLabelOffset;
        },
        
        scaleX(){
            return scaleTime()
                .domain(extent(this.dataSet, this.extractXValue))
                .range([0, this.innerWidth])
                .nice();
        },

        scaleY(){
            return scaleLinear()
                .domain(extent(this.originalData,d => +d[this.yName]))
                .range([this.innerHeight, 0])
                .nice();
        },

        linePath(){
            return this.drawLinePath(this.dataSet);
        }
    },

    methods:{
        bottomTickFormat: timeFormat('%H'),

        extractXValue(d){
            return d[this.xName];
        },

        downloadGraph(){
            const graph = this.graph;
            let source = new XMLSerializer().serializeToString(graph);
            source = '<?xml version="1.0" standalone="no"?>\r\n' + source;
            const url = "data:image/svg+xml;charset=utf-8,"+encodeURIComponent(source);
            const element = document.createElement('a');
            element.setAttribute('href', url);
            element.setAttribute('download',`${this.yName}.svg`);
            element.style.display = 'none';
            document.body.appendChild(element);
            element.click();
            document.body.removeChild(element);
        },

        extractYValue(d){
            return d[this.yName];
        },

        getBottomTickPosition(tick){
            return `translate(${this.scaleX(tick)},0)`;
        },

        getLeftTickPosition(tick){
            return `translate(0,${this.scaleY(tick)})`;
        },
        
        drawLinePath(data){
            const vm = this;
            const path = line()
                .x(d => vm.scaleX(vm.extractXValue(d)))
                .y(d => vm.scaleY(vm.extractYValue(d)))(data);
            return path;
        }
    },

    beforeMount(){
        console.log('lineplot b4 mounted')
        this.dataSet = this.data;
    },

    mounted(){
        console.log('lineplot mounted')
        // this.graph = document.getElementById('graph');
        this.graph = this.$refs['graph'];
    }
}
</script>

<style scoped>
body {
  margin: 0;
  overflow: hidden;
  font-family: 'Poppins', sans-serif;
}

#graph {
    display: inline;
    cursor: pointer;
}

.tick line {
  stroke: #C0C0BB;
}

.tick text {
  fill: #635F5D;
  font-size: 50%;
}

.line-plot path {
  stroke: steelblue;
  stroke-width: 1;
  stroke-linejoin: round;
  stroke-linecap: round;
}

.axis-label {
  font-size: 130%;
  fill: #635F5D;
}
</style>